import React, { Component } from 'react';
import footerlogo from './imgs/common/logo--footer.svg';

class Footer extends Component {
    render(){
        return (
            <footer className="footer-container">
                <div className="footer-wrap">
                    <div className="footer-logo">
                        <img className="fl--image" src={footerlogo} alt="footerlog"/>
                    </div>

                    <div className="footer-menus">
                        <a className="menu" href="./">이용약관</a>
                        <span className="dash">|</span>
                        <a className="menu" href="./">개인정보취급방침</a>
                        <span className="dash">|</span>
                        <a className="menu" href="./">구매자 웹사이트</a>
                    </div>

                    <div className="footer-company">
                        <p>
                            사업장 소재지: 서울시 강남구 봉은사로 331, SH타워 8층(코드링크)
                            <span className="dash">/</span>
                            대표이사: 김동영
                            <span className="dash">/</span>
                            이메일: <a href="mailto:tarzan12@codelinkcorp.com">tarzan12@codelinkcorp.com</a>
                        </p>
                        <p className="company-registration-number">
                            사업자등록번호: 376-87-00776
                            <button className="btn btn-linear check-company">사업자정보 확인</button>
                        </p>
                    </div>
                </div>
            </footer>
        );
    }
}

export default Footer;


